import React, {useRef, useState} from 'react'
import history from './history'
// import { Link } from 'react-router-dom'
import api from './api'

export default function(){
  var nameRef = useRef()
  var passwordRef = useRef()
  var emailRef= useRef ()
  var foodRef=useRef ()
  

   function givemessage(){
     var rgInfo={
      name:nameRef.current.value,
      password: passwordRef.current.value,
      email:emailRef.current.value,
      title:foodRef.current.value
     } 
     console.log(rgInfo)
   
    api.post('/register',rgInfo).then(res=>{
      if(res.data.code === 0){
        history.push('/')
      }else{
        alert('用户名已注册')
      }
     }
    )
  }
  return <div>
   
        用户名：<input type="text" name="name" ref={nameRef}/><br/>
        邮箱：<input type="text" name="email" ref={emailRef}/><br/>
        密码：<input type="password" name="password"ref={passwordRef}/><br/>
        店名：<input type="text" name="name" ref={foodRef}/><br/>
        <button onClick={givemessage}>注册</button>
   
  </div>
}